header {
    background: #000;
}

nav {
    margin: auto 0 0 0;
    padding: 0.25rem;
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

nav li {
    padding: 0.25rem 0.75rem;
}

.nav-container {
    display: flex;
    width: 95vw;
    margin: auto;
    justify-content: space-between;
}

.logo-container {
    padding: 2rem 0.25rem 0.25rem 0.25rem;
}

